.calc_radio {
  margin-top: 3px;
  margin-bottom: 3px;

  input[type="radio"] {
    display: none;

    &:checked {
      + label {
        &::before {
          content: "";
          width: 8px;
          height: 3px;
          border-left: 2px solid $primary;
          border-bottom: 2px solid $primary;
          position: absolute;
          left: 3px;
          top: 8px;
          transform: rotate(-45deg);
          z-index: 1;
        }
      }
    }
  }
}

label {
  font-size: px-to-rem(17px);
  line-height: px-to-rem(24px);
  position: relative;
  padding-left: 25px;
  cursor: pointer;
  display: inline-block;

  &::after {
    width: 16px;
    height: 16px;
    border: 1px solid $primary;
    border-radius: 5px;
    box-sizing: border-box;
    content: "";
    background-color: transparent;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY( -50%);
  }
}

.calc_group {
  padding: 0;
  border: none;
  margin: 20px 0;
}

.calc_title {
  font-size: px-to-rem(14px);
  line-height: px-to-rem(16px);
  font-weight: 700;
  margin-bottom: 10px;
}

.calc_range {
  display: none;

  label {
    @include visually-hidden;
  }
}

.calc_square {
  background-color: transparent;
  display: block;
  width: 100%;
  border-radius: 5px;
  border: 1px solid rgba(255, 255, 255, .2);
  text-align: center;
  font-size: px-to-rem(14px);
  line-height: px-to-rem(16px);
  color: $white;
  padding: 12px 0;
}

.calc_btn {
  @include btn;
}

.calc_result {
  text-align: center;
  font-size: px-to-rem(17px);
  line-height: px-to-rem(24px);
  margin:  20px 0;
}

@media (min-width: 480px) {

  .calc_wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  .calc_title, .calc_input {
    flex-basis: 100%;
    width: 100%;
  }

  .calc_radio {
    margin-right: 15px;

    &:last-child {
      margin-right: 0;
    }
  }

  .calc_btn {
    flex-basis: 164px;
    width: 164px;
    margin-right: 25px;
  }

  .calc_result {
    text-align: left;
    font-size: px-to-rem(14px);
    line-height: px-to-rem(16px);
  }
}

@media (min-width: 1024px) {

  .calc {
    background-color: $black;
    border-radius: 5px;
  }

  .calc_input {
    display: none;
  }

  .calc_range {
    display: flex;
    align-items: center;
    flex-basis: 100%;
    margin-top: 7px;
    margin-bottom: 7px;

    output {
      background-color: transparent;
      display: block;
      width: 67px;
      border-radius: 5px;
      border: 1px solid rgba(255, 255, 255, .2);
      text-align: center;
      font-size: px-to-rem(14px);
      line-height: px-to-rem(16px);
      color: $white;
      padding: 12px 0;
      margin-right: 24px;
    }
  }

  input[type="range"] {
    width: 280px;
    flex-basis: 280px;
    margin: 10px 0;
    appearance: none;

    &::-webkit-slider-runnable-track {
      background-color: $white;
      width: 100%;
      height: 4px;
      cursor: pointer;
      border-radius: 10px;
    }

    &::-moz-range-track {
      background-color: $white;
      width: 100%;
      height: 4px;
      cursor: pointer;
      border-radius: 10px;
    }

    &::-ms-track {
      background-color: $white;
      width: 100%;
      height: 4px;
      cursor: pointer;
      border-radius: 10px;
    }

    &::-webkit-slider-thumb {
      width: 8px;
      height: 8px;
      background-color: $primary;
      border-radius: 8px;
      cursor: pointer;
      appearance: none;
      margin-top: -2px;
    }
    &::-moz-range-thumb {
      width: 8px;
      height: 8px;
      background-color: $primary;
      border-radius: 8px;
      cursor: pointer;
      appearance: none;
      margin-top: -2px;
    }

    &::-ms-thumb {
      width: 8px;
      height: 8px;
      background-color: $primary;
      border-radius: 8px;
      cursor: pointer;
      appearance: none;
      margin-top: -2px;
    }


  }
}




